body {
  scrollbar-width: thin;
  max-width: 100vw;
  overflow-x: hidden;
}

.App {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  text-align: center;
  width: 100%;
  background-color: #fbfbfe;
}

.page {
  scroll-snap-align: start;
  height: 100vh;
  width: 100vw;
}
